.news-section__title {
  margin-bottom: 24px;
}
.news-section__wrapper {
  margin-top: 40px;
  display: flex;
  gap: 32px;
}
.news-section__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.news-section__content:last-child {
  align-self: flex-end;
  justify-self: flex-end;
}
.news-section__cards {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.news-section__block {
  display: flex;
  gap: 32px;
}